<template>
  <div class="container">
    <!-- 卡片 -->
    <el-card>
      <!-- 面包屑 -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/article' }"
          >文章管理</el-breadcrumb-item
        >
        <el-breadcrumb-item>添加文章</el-breadcrumb-item>
      </el-breadcrumb>
      <!-- tag栏切换 -->
      <el-tabs
        :tab-position="'top'"
        style="min-height: 200px; margin-top: 15px"
      >
        <el-tab-pane label="文章标题">
          <!-- 表单信息 -->
          <el-form
            :model="Form"
            ref="FormRef"
            :rules="FormRules"
            label-position="top"
          >
            <el-form-item label="标题" prop="article_Title">
              <el-input
                v-model.trim="Form.article_Title"
                maxlength="50"
                show-word-limit
              ></el-input>
            </el-form-item>
            <el-form-item label="描述" prop="article_describe">
              <el-input
                type="textarea"
                v-model.trim="Form.article_describe"
                maxlength="100"
                show-word-limit
              ></el-input>
            </el-form-item>
            <el-form-item label="分类" prop="article_pId">
              <el-select
                v-model="Form.article_pId"
                placeholder="请选择分类"
                style="width: 100%"
              >
                <el-option
                  v-for="(item, index) in TypeList"
                  :key="index"
                  :label="item.name"
                  :value="item._id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="作者" prop="article_User">
              <el-input
                v-model.trim="Form.article_User.username"
                disabled
              ></el-input>
            </el-form-item>
            <el-form-item label="封面">
              <!-- 上传控件 -->
              <el-upload
                class="upload-demo"
                :action="ActiveUrl"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :on-success="handSuccess"
                list-type="picture"
                :headers="HeaderTokem"
              >
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="文章内容">
          <mavon-editor
            v-model="content"
            ref="md"
            @imgAdd="article_AddCover"
            @change="change"
            style="min-height: 500px"
          ></mavon-editor>
          <el-row style="margin-top: 15px">
            <el-col>
              <el-button
                type="primary"
                @click="Release_Article"
                style="float: right"
                >发布文章</el-button
              >
            </el-col>
          </el-row>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!-- 预览图对话框 -->
    <el-dialog title="预览图" :visible.sync="CoverDialog" width="30%">
      <img :src="ViewURL" alt="error" class="viewImg" />
      <span slot="footer" class="dialog-footer">
        <el-button @click="CoverDialog = false">取 消</el-button>
        <el-button type="primary" @click="CoverDialog = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
// 导入代码高亮模块
// import Hig from 'highlight.js'
// import 'highlight.js/styles/googlecode.css'
export default {
  name: 'Addarticle',
  data() {
    return {
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true, // 预览
      },
      // 文章信息
      Form: {
        article_Title: '',
        article_describe: '',
        article_pId: '',
        article_User: {},
        article_content: '',
        article_cover: '',
      },
      //   接收富文本编辑器输入的内容
      content: '',
      //   表单验证
      FormRules: {
        article_Title: [
          {
            required: true,
            message: '请输入标题',
            trigger: 'blur',
          },
          {
            maxlength: 100,
            message: '长度不能超过50个字符',
            trigger: 'blur',
          },
        ],
        article_describe: [
          {
            required: true,
            message: '请输入描述',
            trigger: 'blur',
          },
          {
            maxlength: 100,
            message: '长度不能超过100个字符',
            trigger: 'blur',
          },
        ],
        article_pId: [
          {
            required: true,
            message: '请选择分类',
            trigger: 'blur',
          },
        ],
        article_User: [
          {
            required: true,
            message: '请输入作者名称',
            trigger: 'blur',
          },
        ],
      },
      // 分类数据
      TypeList: [],
      //   上传文件的请求接口
      ActiveUrl: this.$api.API_SERVER_ARTICLE_FORMIDABLE_UPLOAD,
      // 文件上传的时候携带token值作为请求头
      HeaderTokem: {
        Authorization: this.Storage.Session.get('token'),
      },
      // 控制图片预览的对话框
      CoverDialog: false,
      // 预览图片地址
      ViewURL: '',
    }
  },
  created() {
    // this.GetTypelist()
    // 获取登录用户名
    this.Form.article_User = JSON.parse(this.Storage.Session.get('data'))
    this.TypeList = JSON.parse(this.Storage.Session.get('Type'))
  },
  methods: {
    // //   获取分类数据
    // GetTypelist() {
    //   this.$http.get(this.$api.API_SERVER_ARTICLE_TYPE_LIST).then((res) => {
    //     if (res.data.status != 200) {
    //       return this.$message.error('获取分类失败')
    //     }
    //     this.TypeList = res.data.data.slice(1, 5)
    //     this.$store.commit('GetType',res.data.data.slice(1, 5))
    //   })
    // },
    // 上传成功后触发的事件函数
    handSuccess(file) {
      // 获取上传成功的图片地址
      ;(this.Form.article_cover = file.data.temp_url),
        this.$message.success('上传成功')
    },
    // 预览图
    handlePreview(file) {
      this.CoverDialog = true
      this.ViewURL = this.$api.API_SERVER + file.response.data.view_url
    },
    // 删除图片
    handleRemove(file) {
      const Remove = file.response.data.view_url
      const i = this.Form.article_cover.findIndex((item) => {
        return item.PIC == Remove
      })
      this.Form.article_cover.splice(1, i)
    },

    article_AddCover(pos, file) {
      // const formdata = new formData()
      var formData = new FormData()
      formData.append('file', file)
      this.$http({
        url: this.$api.API_SERVER_ARTICLE_UPLOAD,
        method: 'post',
        data: formData,
        headers: { 'Content-Type': 'multipart/form-data' },
      }).then((res) => {
        this.Url = res.data.data.view_url
        console.log(res.data.data)
        this.$refs.md.$img2Url(
          pos,
          this.$api.API_SERVER + '/' + res.data.data.view_url
        )
      })
    },
    // render为markdown解析后的结果
    change(value, render) {
      this.Form.article_content = render
    },
    // 发布文章
    Release_Article() {
      this.$refs.FormRef.validate((valid) => {
        if (!valid) {
          return this.$message.error('请输入内容在进行发布')
        }
        if (this.Form.article_content.length == 0) {
          return this.$message.error('请输入文章内容才可进行发布!')
        }
        this.$http
          .post(this.$api.API_SERVER_ADD_ARTICLE, this.Form)
          .then((res) => {
            if (res.data.status != 201) {
              return this.$message.error(res.data.msg)
            }
            this.$message.success(res.data.msg)
            this.Form = {
              article_Title: '',
              article_describe: '',
              article_pId: '',
              article_User: {},
              article_content: '',
              article_cover: [],
            }
            this.$router.push('/article')
          })
      })
    },
  },
  mounted() {
    // async function hig() {
    //   await this.Hig
    //   const highlight = document.querySelectorAll('code,pre')
    //   highlight.forEach((item) => {
    //     this.Hig.highlightBlock(item)
    //   })
    // }
    // hig()
  },
}
</script>
<style lang="less" scoped>
.viewImg {
  width: 100%;
  height: 60%;
}
</style>